export default class EindspelController {

    constructor($state) {
        "ngInject";
        this.score = $state.params.score;
    }

    $onInit() {
        this.antwoorden = ["Michael Bay", "Slade Wilson", "Spock", "Nintendo", "Agumon"];
        this.jokers = ["vacature", "samen sterk voor werk", "Spock", "Nintendo", "Agumon"];
        this.gegevenAntwoorden = [false, false, false, false];
        this.wachtwoord3Correct = false;
        this.blokkeerButton3 = false;
    }

    isAntwoordCorrect(actual, expectedIndex) {
        if (this.inputCorrectOfGekocht(actual, expectedIndex)) {
            this.gegevenAntwoorden[expectedIndex] = true;
            return true;
        }
        this.gegevenAntwoorden[expectedIndex] = false;
        return false;
    }

    inputCorrectOfGekocht(actual, expectedIndex) {
        return actual && this.antwoordIsJokerOfWachtwoord(actual, expectedIndex);
    }

    antwoordIsJokerOfWachtwoord(actual, expectedIndex) {
        return actual.toLowerCase() === this.antwoorden[expectedIndex].toLowerCase()
            || actual.toLowerCase() === this.jokers[expectedIndex].toLowerCase()
            || this.wachtwoord3Correct === true
            || this.gegevenAntwoorden[expectedIndex] === true;
    }

    getCommentaarVraag1() {
        const isCorrect = this.isAntwoordCorrect(this.antwoord1, 0);
        if (isCorrect) {
            return "ANTWOORD CORRECT";
        }
        return "";
    }

    getCommentaarVraag2() {
        const isCorrect = this.isAntwoordCorrect(this.antwoord2, 1);
        if (isCorrect) {
            return "ANTWOORD CORRECT";
        }
        return "";
    }

    getCommentaarVraag3() {
        const isCorrect = this.isAntwoordCorrect(this.antwoord3, 2);
        if (isCorrect) {
            return "ANTWOORD CORRECT";
        }
        return "";
    }

    getCommentaarVraag4() {
        const isCorrect = this.isAntwoordCorrect(this.antwoord4, 3);
        if (isCorrect) {
            return "ANTWOORD CORRECT";
        }
        return "";
    }

    getCommentaarVraag5() {
        const isCorrect = this.isAntwoordCorrect(this.antwoord5, 4);
        if (isCorrect) {
            return "ANTWOORD CORRECT";
        }
        return "";
    }

    questionCorrect(index) {
        return this.gegevenAntwoorden[index - 1] === true;
    }

    alleVragenCorrect() {
        return this.gegevenAntwoorden.indexOf(false) === -1;
    }

    notEnoughPoints() {
        return this.score < 100;
    }

    unlock(index) {
        this.score -= 100;
        this.gegevenAntwoorden[index - 1] = true;
    }

    checkWachtwoord() {
        if (this.antwoord3 && this.antwoord3.toLowerCase() === "arbeidsbemiddeling") {
            this.wachtwoord3Correct = true;
        }
        this.blokkeerButton3 = true;
    }
}

