$templateCache.put("app/score/score.html","<div class=\"col-md-6 col-md-offset-4\">\n<div>\n<h2>Spelregels</h2>\n<p>U krijgt zometeen vijf keer twee foto\'s te zien. De rechtse foto van het paar is telkens een voorwerp dat is verborgen op de plek die te zien is op de linkse foto. Dit is steeds een plek die zich in of rond dit gebouw bevindt.\n</p>\n<p>Als u het voorwerp vindt, vindt u tevens een wachtwoord dat u kan ingeven in het invoerveld rechts van de foto\'s.</p>\n<p>Als de vijf wachtwoorden correct zijn ingegeven, verschijnt er onderaan de pagina een zesde foto van een locatie.</p>\n<p>Op deze locatie vindt u de eindprijs.</p>\n<p>Opgelet: alle groepen doen deze proef gelijktijdig. Slechts één groep kan deze proef winnen. Snelheid is dus van belang!</p>\n<p>Indien u tijdens het voorbije uur wachtwoorden hebt verdient door puzzels op te lossen, kan u deze ingeven in de invoervelden die op de wachtwoorden staan beschreven. Voor deze wachtwoorden hoeft u dus niet langer het gebouw te doorzoeken.\n</p>\n<p>Bericht voor de mol: Mollen mogen enkel de proeven van hun eigen groep saboteren. De voorwerpen of wachtwoorden verstoppen voor de andere groepen is dus niet toegelaten. Dit verbod op sabotage geldt ook voor de niet-mollen.</p>\n<p>Wie in HTML/JS gaat neuzen, wordt automatisch gediskwalificeerd!</p>\n<form class=\"form-inline\"ng-submit=\"$ctrl.startEindspel()\">\n<h3>Lees de spelregels aandachtig na vooraleer het spel te beginnen</h3>\n<button type=\"submit\"class=\"btn btn-danger\">Start de finale proef</button>\n</form>\n<img src=\"./../../content/images/logo.png\"class=\"img-responsive\"alt=\"Responsive image\">\n<p><i>Notitiam de moralibus</i></p>\n<p><i>-Adzean Awry Kirk III, CEO (1527 - 1601)</i></p>\n</div>\n</div>");
